import React, { memo, ChangeEvent, KeyboardEvent, useState } from "react"
import { IconButton, TextField } from "@mui/material"
import { AddBox } from "@mui/icons-material"
import { RejectValueType } from "common/utils/create-app-async-thunk"

const addButtonStyles = {
  maxWidth: "30px",
  maxHeight: "30px",
  minWidth: "30px",
  minHeight: "30px",
}

//  
export const AddItemForm: React.FC<Props> = memo((props: Props) => {
  let [titleTask, setTitleTask] = useState("")
  let [error, setError] = useState<string | null>(null)

  const addItemHandler = () => {
    if (titleTask.trim() !== "") {
      props
        .addItem(titleTask.trim())
        .then(() => {
          setTitleTask("")
        })
        .catch((err: RejectValueType) => {
          if (err.data) {
            const messages = err.data.messages
            setError(messages.length ? messages[0] : "Some error occurred")
          }
        })
    } else {
      setError("Error!")
    }
  }

  const onChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
    setTitleTask(event.currentTarget.value)
  }

  const onKeyPressHandler = (event: KeyboardEvent<HTMLInputElement>) => {
    if (error !== null) {
      setError(null)
    }
    if (event.key === "Enter") {
      addItemHandler()
    }
  }

  return (
    <div>
      {/*        */}
      <TextField
        variant={"outlined"}
        value={titleTask}
        onChange={onChangeHandler}
        onKeyDown={onKeyPressHandler}
        error={!!error}
        label={error ? "Title is required" : "Please type you title"}
        disabled={props.disabled}
        helperText={error}
      />
      {/*   */}
      <IconButton onClick={addItemHandler} color={"primary"} style={addButtonStyles} disabled={props.disabled}>
        <AddBox />
      </IconButton>
    </div>
  )
})

//types
type Props = {
  addItem: (titleTodolist: string) => Promise<any>
  disabled?: boolean
}
